.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 50;
    background-color: var(--white-80);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border-bottom: 1px solid var(--stone-100);
}

.navbar-container {
    height: 80px;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    font-size: 1.25rem;
    font-weight: 600;
    letter-spacing: -0.025em;
}

.nav-links {
    display: none;
    align-items: center;
    gap: 2rem;
}

@media (min-width: 768px) {
    .nav-links {
        display: flex;
    }
}

.nav-link {
    font-size: 0.875rem;
    font-weight: 500;
    color: var(--text-secondary);
    transition: color 0.2s;
}

.nav-link:hover {
    color: var(--black);
}

.nav-actions {
    display: flex;
    align-items: center;
    gap: 1.25rem;
}

.icon-btn {
    color: var(--neutral-900);
    transition: color 0.2s;
    display: flex;
    align-items: center;
}

.icon-btn:hover {
    color: #525252;
    /* neutral-600 */
}

.signin-btn {
    display: none;
    height: 2.25rem;
    padding: 0 1rem;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    border: 1px solid var(--neutral-200);
    background-color: var(--bg-white);
    font-size: 0.875rem;
    font-weight: 500;
    transition: background-color 0.2s;
}

@media (min-width: 640px) {
    .signin-btn {
        display: inline-flex;
    }
}

.signin-btn:hover {
    background-color: var(--stone-50);
}