.testimonial {
    padding: 6rem 0;
    border-top: 1px solid var(--stone-100);
}

.testimonial-container {
    padding: 0 1.5rem;
}

.testimonial-grid {
    display: grid;
    grid-template-cols: 1fr;
    gap: 4rem;
    align-items: center;
}

@media (min-width: 1024px) {
    .testimonial-grid {
        grid-template-cols: 1fr 1fr;
    }
}

.quote-side {
    display: flex;
    flex-direction: column;
}

.quote-icon {
    color: var(--stone-200);
    margin-bottom: 2rem;
}

.quote-text {
    font-size: 1.875rem;
    line-height: 1.25;
    font-weight: 500;
    letter-spacing: -0.025em;
    margin-bottom: 2rem;
}

@media (min-width: 768px) {
    .quote-text {
        font-size: 2.25rem;
    }
}

.author-info {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.author-name {
    font-size: 1rem;
    font-weight: 600;
    color: var(--neutral-900);
}

.author-title {
    font-size: 0.875rem;
    color: var(--text-secondary);
}

.signature {
    margin-left: auto;
    font-family: 'Brush Script MT', cursive;
    font-size: 1.5rem;
    color: var(--stone-400);
    opacity: 0.6;
}

.philosophy-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    min-height: 300px;
    padding: 2.5rem;
    background-color: var(--stone-50);
    border-radius: 2rem;
    position: relative;
    overflow: hidden;
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    z-index: 10;
}

.card-number {
    font-size: 3.75rem;
    font-weight: 300;
    letter-spacing: -0.05em;
    color: var(--stone-200);
}

.card-label {
    font-size: 0.875rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: var(--text-secondary);
}

.card-content {
    z-index: 10;
    margin-top: auto;
}

.card-heading {
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 0.75rem;
    letter-spacing: -0.025em;
}

.card-desc {
    color: var(--text-secondary);
    line-height: 1.6;
    font-size: 1.125rem;
}

.read-more {
    margin-top: 1.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.875rem;
    font-weight: 500;
    cursor: pointer;
    transition: gap 0.2s;
}

.read-more:hover {
    gap: 1rem;
}

.card-decor {
    position: absolute;
    bottom: -5rem;
    right: -5rem;
    width: 16rem;
    height: 16rem;
    background-color: var(--stone-200);
    border-radius: 9999px;
    filter: blur(48px);
    opacity: 0.5;
    pointer-events: none;
}