.features {
    padding: 6rem 0;
    background-color: var(--black);
    color: #ffffff;
}

.features-container {
    padding: 0 1.5rem;
}

.features-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 4rem;
}

@media (min-width: 768px) {
    .features-header {
        flex-direction: row;
    }
}

.features-title {
    font-size: 2.25rem;
    font-weight: 500;
    letter-spacing: -0.025em;
}

@media (min-width: 768px) {
    .features-title {
        font-size: 3rem;
    }
}

.view-all {
    display: none;
    align-items: center;
    gap: 0.5rem;
    color: var(--stone-400);
    transition: color 0.2s;
    padding-bottom: 0.25rem;
    border-bottom: 1px solid transparent;
}

@media (min-width: 768px) {
    .view-all {
        display: inline-flex;
    }
}

.view-all:hover {
    color: #ffffff;
    border-bottom-color: #ffffff;
}

.features-grid {
    display: grid;
    grid-template-cols: 1fr;
    gap: 1px;
    background-color: var(--neutral-800);
    border: 1px solid var(--neutral-800);
    border-radius: 1rem;
    overflow: hidden;
}

@media (min-width: 768px) {
    .features-grid {
        grid-template-cols: repeat(3, 1fr);
    }
}

.feature-card {
    background-color: var(--neutral-900);
    padding: 2rem;
    transition: background-color 0.2s;
    cursor: pointer;
}

@media (min-width: 768px) {
    .feature-card {
        padding: 2.5rem;
    }
}

.feature-card:hover {
    background-color: rgba(38, 38, 38, 0.8);
}

.icon-wrapper {
    width: 3rem;
    height: 3rem;
    background-color: var(--neutral-800);
    border-radius: 9999px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
    transition: transform 0.2s;
}

.feature-card:hover .icon-wrapper {
    transform: scale(1.1);
}

.feature-icon {
    color: #ffffff;
}

.feature-name {
    font-size: 1.25rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.feature-desc {
    color: var(--neutral-400);
    line-height: 1.6;
}