.hero {
    padding-top: 8rem;
    padding-bottom: 4rem;
    padding-left: 1rem;
    padding-right: 1rem;
    overflow: hidden;
}

@media (min-width: 768px) {
    .hero {
        padding-top: 10rem;
        padding-bottom: 6rem;
    }
}

.hero-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 4rem;
    gap: 2rem;
}

@media (min-width: 768px) {
    .hero-header {
        flex-direction: row;
        margin-bottom: 6rem;
    }
}

.circular-element {
    display: none;
    width: 6rem;
    height: 6rem;
    position: relative;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
}

@media (min-width: 768px) {
    .circular-element {
        display: flex;
    }
}

.rotating-svg {
    width: 100%;
    height: 100%;
    animation: spin 10s linear infinite;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.rotating-text {
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    fill: var(--neutral-900);
    font-weight: 500;
}

.play-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-left: 2px;
}

.title-wrapper {
    flex: 1;
    text-align: center;
}

.hero-title {
    font-size: 3rem;
    font-weight: 500;
    letter-spacing: -0.025em;
    line-height: 0.95;
}

@media (min-width: 768px) {
    .hero-title {
        font-size: 4.5rem;
    }
}

@media (min-width: 1024px) {
    .hero-title {
        font-size: 6rem;
    }
}

.italic-text {
    color: var(--neutral-400);
    font-style: italic;
    font-weight: 400;
}

.desktop-only {
    display: none;
}

@media (min-width: 768px) {
    .desktop-only {
        display: block;
    }
}

.social-proof {
    display: none;
    align-items: center;
    gap: 0.75rem;
    flex-shrink: 0;
}

@media (min-width: 768px) {
    .social-proof {
        display: flex;
    }
}

.avatar-stack {
    display: flex;
    margin-left: 0.75rem;
}

.avatar {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 9999px;
    border: 2px solid var(--bg-white);
    object-fit: cover;
    margin-left: -0.75rem;
}

.plus-avatar {
    background-color: var(--black);
    color: var(--bg-white);
    display: flex;
    align-items: center;
    justify-content: center;
}

.social-text {
    font-size: 0.75rem;
    color: var(--text-secondary);
    line-height: 1.25;
}

.social-text .count {
    display: block;
    font-weight: 600;
    color: var(--black);
}

.bento-grid {
    display: grid;
    grid-template-cols: 1fr;
    gap: 1rem;
    min-height: 600px;
}

@media (min-width: 768px) {
    .bento-grid {
        grid-template-cols: repeat(6, 1fr);
        gap: 1.5rem;
    }
}

@media (min-width: 1024px) {
    .bento-grid {
        grid-template-cols: repeat(12, 1fr);
    }
}

.grid-item {
    position: relative;
    overflow: hidden;
    border-radius: 2rem;
    transition: transform 0.7s;
}

.grid-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.7s;
}

.grid-item:hover .grid-img {
    transform: scale(1.05);
}

.tall-left {
    height: 400px;
}

@media (min-width: 768px) {
    .tall-left {
        grid-column: span 3;
        height: auto;
    }
}

.img-overlay {
    position: absolute;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.05);
    transition: background-color 0.2s;
}

.grid-item:hover .img-overlay {
    background-color: rgba(0, 0, 0, 0.1);
}

.badge-wrapper {
    position: absolute;
    bottom: 1.5rem;
    left: 1.5rem;
}

.badge {
    display: inline-flex;
    padding: 0.25rem 0.75rem;
    background-color: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(4px);
    border-radius: 9999px;
    font-size: 0.75rem;
    font-weight: 500;
    letter-spacing: 0.025em;
}

.middle-top {
    height: 300px;
}

@media (min-width: 768px) {
    .middle-top {
        grid-column: span 3;
        height: 60%;
        border-bottom-left-radius: 0;
    }
}

.explore-card {
    height: 300px;
    background-color: var(--stone-100);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    text-align: center;
}

@media (min-width: 768px) {
    .explore-card {
        grid-column: span 3;
        height: 60%;
    }
}

.rotating-asterisk {
    animation: spin 8s linear infinite;
    color: var(--neutral-900);
}

.card-title {
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
    letter-spacing: -0.025em;
}

.card-desc {
    color: var(--text-secondary);
    font-size: 0.875rem;
    margin-bottom: 2rem;
    max-width: 200px;
}

.shop-btn {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    background-color: var(--black);
    color: var(--bg-white);
    padding: 0.75rem 1.5rem;
    border-radius: 9999px;
    font-size: 0.875rem;
    font-weight: 500;
    transition: all 0.3s;
}

.shop-btn:hover {
    background-color: #262626;
    padding-right: 2rem;
}

.right-tall {
    height: 400px;
}

@media (min-width: 768px) {
    .right-tall {
        grid-column: span 3;
        height: auto;
    }
}

.arrow-btn-wrapper {
    position: absolute;
    bottom: 1.5rem;
    right: 1.5rem;
}

.arrow-btn {
    width: 2.5rem;
    height: 2.5rem;
    background-color: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(4px);
    border-radius: 9999px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s;
}

.arrow-btn:hover {
    background-color: var(--bg-white);
}

.bottom-left,
.bottom-right {
    height: 250px;
}

@media (min-width: 768px) {

    .bottom-left,
    .bottom-right {
        grid-column: span 3;
        height: auto;
    }
}

.opacity-90 {
    opacity: 0.9;
}